.wrap-header {
    height: 78px;
}
.logo-header{
	position: absolute;
	left: 40px;
	top: 0;
	z-index: 1;
	a{
		display: inline-block;
	}
	img {
	    height: 78px;
	}
}
.contact-menu{
	position: relative;
}
.contact{
	text-align: right;
	ul{
		padding-right: 40px;
		background-color: #f0f0f0;
    	border-bottom: 1px solid #e3e3e3;;
		li{
			display: inline-block;
			line-height: 33px;
			color: #808080;
			font-size: 12px;
			cursor: pointer;
			&:first-child{
				margin-right: 40px;
				&:hover{
					color: #ED3239;
				}
			}
		}
	}
}
i.fab.fa-facebook-f {
    background: #3B5998;
    color: #fff;
    padding: 4px 7px;
    margin-right: 7px;
}
i.fab.fa-youtube {
    background: #BB0000;
    color: #fff;
    padding: 4px;
}
.menu{
	text-align: right;
	// margin-bottom: 29px;
	ul{
		line-height: 44px;
		padding-right: 40px;
		li{
			display: inline-block;
			line-height: 38px;
		    margin-left: 53px;
		    font-size: 14px;
		    color: #5e5e5e;
		    font-weight: bold;
		    cursor: pointer;
		    &:hover{
		    	color: #ED3239;
		    }
		}
	}
}