@media screen and (max-width: 768px) {
    //Khanh res
    #header {
        .menu-res {
            display: block;
        }
        .menu {
            height: 44px;
            transition: 0.5s;
            nav {
                height: 44px;
            }
            ul {
                position: absolute;
                display: none;
                padding: 0;
                width: 200px;
                z-index: 3;
                background: #ed3239;
                text-align: center;
                right: 15px;
                top: 78px;
                li {
                    color: #fff;
                    display: block;
                    border-bottom: 1px solid #0e0e0e47;
                    margin: 0;
                }
            }
        }
        .contact ul {
            padding-right: 15px;
        }
    }
    #footer {
        .camket-baohanh ul {
            padding: 0 15px;
        }
        .sanpham__footer .row {
            margin: 0;
            .col-2 {
                width: 33.333%;
                margin-bottom: 15px;
                &:nth-child(4),
                &:nth-child(5) {
                    width: 50%;
                }
            }
        }
    }
    #lienhe {
        .address__lienhe {
            padding-left: 15px;
        }
    }
    //Tuan res
    .p-detail__video iframe {
        height: 350px;
    }
    .p-detail__inner li {
        width: 48%;
        text-align: center;
    }
    .p-detail__inner li:nth-child(2) {
        margin-left: 1%;
    }
    .specifications__left,
    .specifications__right,
    .p-detail__row--col1,
    .p-detail__row--col2 {
        width: 100%;
        float: unset;
    }
    //page GioiThieu--byTuan
    #gioithieu,#tintucchitiet{
        .tintuc__gioithieu {
            width: 100%;
            float: none;
            margin-top: 50px;
        }
        .content__gioithieu {
            width: 100%;
            float: none;
            article h2 {
                text-align: center;
                padding-top: 25px;
            }
        }
        .right__box2 img {
            width: unset;
        }
        .right__box3 li {
            padding: 30px 15px;
            margin-top: 25px;
            float: left;
            width: 49%;
            border: 1px solid #ccc;
        }
        .right__box3 ul {
            overflow: hidden;
        }
        .right__box3 ul li:nth-child(2n):not(:first-child) {
            margin-left: 2%;
        }
        .right__box4 {
            display: none;
        }
        .content__gioithieu__video iframe{
            height:375px;
        }
    }
    //Chuc res
    #tin__tuc {
        .right__box3 .product {
            width: 33.3333%;
            margin-left: unset;
        }
    }
    #hethongdetail {
        .hethong__store1--text {
            h3 {
                font-size: 25px;
            }
            h2 {
                font-size: 35px;
            }
        }
    }
    #hethongeach {
        .hethong__search {
            width: 55%;
            float: left;
            margin-top: 10px;
        }
        .hethong__store1--text {
            h3 {
                font-size: 25px;
            }
            h2 {
                font-size: 35px;
            }
        }
        .depart {
            width: 60%;
        }
        .contact--infor {
            width: 40%;
            padding-top: 0px;
        }
    }
}

