@media screen and (max-width: 640px) {
    //Khanh res
    #footer {
        .camket-baohanh li {
            padding: 0 10px;
        }
    }
    #lienhe {
        .col-6 {
            width: 100%;
        }
    }
    //byTuan res
    .products {
        &__banner .products__path {
            bottom: 50%;
            left: 50%;
            transform: translate(-50%, 50%);
        }
        &__left {
            width: 100%;
        }
        &__filter {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            transition: .3s;
            overflow: hidden;
            &__inner {
                width: 33.33%;
                float: left;
            }
            &__icon {
                display: block;
                text-align: center;
                border: 1px solid #ccc;
                font-size: 18px;
                cursor: pointer;
                padding-top:10px;
                padding-bottom: 10px;
                i{
                    font-size: 25px;
                    margin-right: 10px;
                    color: #ed3239;
                }
            }
            &__inner {
                display: none;
            }
            & div:nth-child(3n+2):not(:nth-child(2)) {
                clear: both;
            }
        }
        &__right {
            width: 100%;
            margin-top: 85px;
        }
        &__inner {
            position: relative;
            .container {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .news {
        margin-top: 50px;
        .heading {
            font-size: 18px;
        }
        &__list {
            &__item {
                width: 50%;
                float: left;
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }
    .content__gioithieu{
        &__left{
            width:100%;
        }
        &__right{
            width:100%;
        }
    }
    //Chuc res
    #hethong {
        .hethong__store1--text {
            h3 {
                font-size: 25px;
            }
            h2 {
                font-size: 35px;
            }
        }
        .hethong__store1--inner {
            height: 180px;
        }
        .hethong__search--inner {
            width: 70%;
        }
    }
}

