// slider
.slide__home {
	position: relative;
	.owl-dots {
	    position: absolute;
	    bottom: 5px;
	    left: 50%;
	    transform: translateX(-50%);
	    background: rgba(0, 0, 0, 0.16) none repeat scroll 0 0;
	    border-radius: 20px;
	    padding: 0 5px;
	}
	.owl-nav {
	    position: absolute;
	    top: 39%;
	    height: 0px;
	    width: 100%;
	    margin: 0;
	    .owl-prev{
	    	color: transparent;
	    	span{
	    		color: transparent;
	    	}
	    }
	    .owl-next{
	    	color: transparent;
	    	span{
	    		color: transparent;
	    	}
	    }
	}
	.owl-theme .owl-dots .owl-dot span {
	    margin: 2px;
	}
	.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
	    background: #ed3239;
	}
	.owl-theme .owl-dots .owl-dot span {
	    background: #fff;
	}
	.owl-theme .owl-dots .owl-dot{
		margin-top: 2px;
	}
}
.owl-carousel .owl-nav button.owl-next {
    position: absolute;
    right: 0;
}

.owl-carousel .owl-nav button.owl-prev {
    position: absolute;
    left: 0;
}
.owl-prev span:before {
    content: "";
    position: absolute;
    border-left: 2px solid #13101036;
    border-bottom: 2px solid #13101036;
    height: 30px;
    width: 30px;
    transform: rotate(45deg);
    left: 12px;
    top: 21px;
}
.owl-next span:before{
	content: "";
    position: absolute;
    border-right: 2px solid #13101036;
    border-bottom: 2px solid #13101036;
    height: 30px;
    width: 30px;
    transform: rotate(-45deg);
    right: 12px;
    top: 21px;
}
.owl-carousel .owl-nav button.owl-prev,.owl-carousel .owl-nav button.owl-next {
    margin: 0;
    height: 73px;
    margin: 0 5px;
    width: 37px;
    background: #4e48482e;
    &:hover{
    	background: #4e48482e;
    }
}

